<template>
  <div class="InvoiceClip">
    <div class="navbar">
      <span :class="{isActive:isActive=='待处理'?true:false}" @click="ToBeTreated">待处理</span>
      <span :class="{isActive:isActive=='已完成'?true:false}" @click="Completed">已完成</span>
    </div>
    <div class="content" :style="{height:heightVal+'px'}">
      <div class="item" @touchstart.native="showDeleteButton(1)" @touchend.native="clearLoop(1)">
        <div class="title">
          <span  class="borderGules"></span>
          <span style="fontSize:11px;">专</span>
          <span>天津啦啦啦啦剧场有限公司</span>
        </div>
        <div class="item_content">
          <div class="left">
            <span>开票日期：</span>
            <span>2018-09-08</span>
          </div>
          <div class="right">
            <span>税价合计：</span>
            <span style="color:#E51C23">￥48578435743</span>
          </div>
        </div>
      </div>

      <div class="item" @touchstart.native="showDeleteButton(2)" @touchend.native="clearLoop(2)">
        <div class="title">
          <span  class="borderOrange"></span>
          <span style="fontSize:11px;">普</span>
          <span>天津啦啦啦啦剧场有限公司</span>
        </div>
        <div class="item_content">
          <div class="left">
            <span>开票日期：</span>
            <span>2018-09-08</span>
          </div>
          <div class="right">
            <span>税价合计：</span>
            <span style="color:#E51C23">￥48578435743</span>
          </div>
        </div>
      </div>

      <div class="item" @touchstart.native="showDeleteButton(3)" @touchend.native="clearLoop(3)">
        <div class="title">
          <span class="borderBlue"></span>
          <span style="fontSize:11px;">卷</span>
          <span>天津啦啦啦啦剧场有限公司</span>
        </div>
        <div class="item_content">
          <div class="left">
            <span>开票日期：</span>
            <span>2018-09-08</span>
          </div>
          <div class="right">
            <span>税价合计：</span>
            <span style="color:#E51C23">￥48578435743</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
// import { Button, Toast } from "mint-ui";

export default {
  data() {
    return {
      heightVal: "",
      isActive: "待处理"
    };
  },
  methods: {
    ToBeTreated() {
      //待处理
      this.isActive = "待处理";
    },
    Completed() {
      //已完成
      this.isActive = "已完成";
    },
    showDeleteButton(e) {
      clearTimeout(this.Loop); //再次清空定时器，防止重复注册定时器
      this.Loop = setTimeout(function(){
        alert(123);
      },1000);
    },
    clearLoop(e){
      clearTimeout(this.Loop);
    }
  },
  mounted() {
    this.heightVal = window.screen.availHeight - 132;
  }
};
</script>

<style lang="less" scoped>
.InvoiceClip {
  text-align: center;
}
.navbar {
  height: 45px;
  line-height: 45px;
  display: flex;
  left: 72px;
  color: #101010;
  font-size: 14px;
  font-family: Arial;
  width: 100%;
  padding-bottom: 5px;
  span {
    display: block;
    flex: 1;
    &:first-child {
      border-right: solid 1px #bbbbbb;
    }
  }
}
.content {
  background-color: #f2f3f5;
  padding-top: 14px;
  overflow-y: auto;
  .item {
    background-color: #fff;
    width: 97%;
    margin:0 auto;
    color: rgba(16, 16, 16, 1);
    font-size: 12px;
    font-family: Arial;
    margin-bottom: 10px;
    .title {
      font-size: 13px;
      text-align: left;
      position: relative;
      padding-left: 30px;
      height:43px;
      line-height: 43px;
      border-bottom: solid 1px #ECECEC;
      span:first-child{
        position: absolute;
        left: 0px;
        top: 0px;
        width:0;
        height:0;
        border-width:0 0 30px 30px;
        border-style:solid;
      }
      span:nth-child(2){
        position: absolute;
        left: 2px;
        top: -10px;
        z-index: 2;
        color:#fff;
      }
      .borderGules{
        border-color:transparent transparent transparent #F4585D;
      }
      .borderOrange{
        border-color:transparent transparent transparent #FF9800;
      }
      .borderBlue{
        border-color:transparent transparent transparent #7ABAFF;
      }
    }
    .item_content {
      display: flex;
      height:43px;
      line-height: 43px;
      .left {
        flex: 1;
      }
      .right {
        flex: 1;
      }
    }
  }
}

.isActive {
  color: #409cff;
}
</style>



